  .loading-bar {
    width: 100%;
    height: 4px;
    background-color: #f0f0f0;
    position: relative;
    margin-top: 10px;
  }
  
  .loading-bar .bar {
    width: 0;
    height: 100%;
    background-color: #007bff;
    animation: loading 1.5s infinite;
  }
  
  @keyframes loading {
    0% {
      width: 0%;
    }
    50% {
      width: 50%;
    }
    100% {
      width: 100%;
    }
  }

