.reviews_whole
{
    height: 100%;
    width: 100%;
}

.display_Revs
{
    height: 90%;
    overflow: hidden;
    overflow-y: scroll;
}

.REV_write_post
{
    height: 100%;
    display: flex;
    justify-content: center;
    gap: 10px;
}

.REV_bottom
{
    height: 10%;
}

.write_status_message
{
    resize: none;
    outline: none;
    background-color: transparent;
    border-radius: 10px;
    border-style: solid;
    border-width: 2px;
    border-color: rgb(34, 122, 255);
    width: 70%;
    color: rgb(34, 122, 255);
    font-family: Arial, Helvetica, sans-serif;
    height: 50%;
}

.Go_SM
{
    background-color: transparent;
    border-style: solid;
    border-width: 2px;
    border-color: rgb(34, 122, 255);
    border-radius: 10px;
    color: rgb(34, 122, 255);
    flex-shrink: 0;
    padding: 2.5%;
    height: 30%;
}

.inst
{
    background-color: rgb(70, 70, 70);
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
}

.posts
{
    background-color: rgb(74, 112, 249);
    padding: 10px;
    border-radius: 10px; 
    margin-bottom: 10px;
}

.posts p
{
    font-size: 14px;
}

.inst p
{
    color: rgb(129, 129, 129);
    text-align: center;
    font-size: 13px;
}

.poster_PP
{
    height: 30px;
    width: 30px;
    border-radius: 10px;
    background-color: black;
}

.poster_PP img
{
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.poster
{
    display: flex;
    align-items: center;
    gap: 10px;
}

.poster p
{
    font-weight: 600;
    font-size: 14px;
    color: rgb(1, 15, 146);
}

.p_vote
{
    display: flex;
    gap: 20px;
    align-items: center;
}

.p_PST_quote
{
    width: 80%;

}

.p_PST_reply
{
    background-color: rgb(38, 97, 198);
    padding: 5px;
    border-radius: 10px;
    color: #002e4d;
}

.up_ICON
{
    font-weight: 700;
    font-size: 30px;
    color: rgb(3, 3, 173);
}

.inb_Rep
{
    display: flex;
    gap: 10px;
}

.rep_Digs
{
    display: flex;
    gap: 5px;
}

.inb_P
{
    color: rgb(42, 0, 169);
    font-weight: 600;
}

/* Add this CSS to your stylesheet */
.spinner {
    border: 2px solid #f3f3f3; /* Light grey */
    border-top: 2px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 10px;
    height: 10px;
    animation: spin 1s linear infinite;
    margin: 20px auto; /* Center it */
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.reply_conts
{
    width: 95%;
    background-color: rgb(67, 67, 67);
    padding: 10px;
    border-radius: 10px;
    position: fixed;
    top: 80%;
    z-index: 9;
    color: rgb(126, 195, 255);
    margin: 0 auto;
    font-size: 13px;
}
