.send_message_inner
{
    height: 100%;
    width: 100%;
  
}

.profile_img
{
    height: 30px;
    width: 30px;
    border-radius: 10px;
    background-color: black;
}

.profile_img img
{
    height: 100%;
    width: 100%;
    object-fit: cover;
}
.message_name
{
    color: rgb(127, 136, 255);
    font-size: 12px;
}
.S_M_profilebar
{
    display: flex;
    align-items: center;
    gap: 10px;
    width: 50%;
    backdrop-filter: blur(4px);
    background: rgba(0, 30, 178, 0.3);
    padding-left: 5px;
}

.inner_message_PR
{
    height: 35%;
    width: 100%;
    backdrop-filter: blur(4px);
    background: rgba(0, 0, 0, 0.3);
    color: rgb(34, 122, 255);
    overflow: hidden;
    overflow-y: scroll;
    padding: 10px;
}

.message_BR_ST
{
    height: 20%;
    backdrop-filter: blur(4px);
    background: rgba(0, 0, 0, 0.3);
    display: flex;
    justify-content: center;
    gap: 10px;
}

.write_status_message
{
    resize: none;
    outline: none;
    background-color: transparent;
    border-radius: 10px;
    border-style: solid;
    border-width: 2px;
    border-color: rgb(34, 122, 255);
    width: 70%;
    color: rgb(34, 122, 255);
    font-family: Arial, Helvetica, sans-serif;
    padding: 10px;
}

.Go_SM
{
    background-color: transparent;
    border-style: solid;
    border-width: 2px;
    border-color: rgb(34, 122, 255);
    border-radius: 10px;
    color: rgb(34, 122, 255);
    flex-shrink: 0;
    padding: 10px;
}
.SMII
{
    height: 100%;
}