.MTT_whole
{
    height: 100%;
    width: 100%;
}

.message_ICN
{
    height: 10%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.disp_TTS
{
    height: 90%;
    overflow: hidden;
    overflow-y: scroll;
   
}

.messages_div
{
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 10px;
    background-color: rgb(54, 54, 54);
    display: flex;
    align-items: center;
    gap: 10px;
}

.prof_div
{
    height: 40px;
    width: 40px;
    border-radius: 10px;
    background-color: rgb(83, 83, 83);
}

.prof_div img
{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.messenger
{
    color: rgb(103, 156, 255);
    font-weight: 500;
}

.mess
{
    color: rgb(97, 97, 97);
    font-size: 14px;
}

.mess_TO
{
    color: rgb(151, 151, 151);
    font-size: 14px;
    font-weight: 700; 
}

.name_mess
{
    width: 75%;
  
}

.messages_num
{
    color: rgb(103, 156, 255);
    font-size: 12px;
}

.Display_messages
{
    height: 100%;
    width: 100%;
    backdrop-filter: blur(5px);
    background: rgba(0, 0, 0, 0.5);
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    position: fixed;
    top: 0;
    z-index: 9;
    overflow: hidden;
}

.inner_DM
{
    height: 100%;
    width: 100%;
}

.profile_messager
{
    height: 10%;
    width: 100%;
    display: flex;
    align-items: center;
    gap: 10px;
}

.DM_body
{
    height: 80%;
    overflow: hidden;
    overflow-y: scroll;
}

.sendmessage_DM
{
    height: 10%;
    width: 100%;
   
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.Messenger_DIV
{
    height: 30px;
    width: 30px;
    border-radius: 10px;
    background-color: rgb(99, 99, 99);
}

.name_MESS
{
    color: rgb(120, 120, 120);
}

.messages_DIV
{
    width: 80%;
    background-color: rgb(127, 140, 255);
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
    position: relative;
    left: 15%;
}

.messages_DIV_Text
{
    font-size: 13px;
}

.messages_DIV_FRM
{
    width: 80%;
    background-color: rgb(113, 113, 113);
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 10px;
}

.DM_INNER_BODY
{
    margin-bottom: 50%;
}

.rep_from
{
    padding: 5px;
    justify-content: end;
    display: flex;
    font-size: 12px;
    background-color: rgb(96, 96, 96);
    border-radius: 10px;
    color: rgb(49, 49, 49);
}

.rep_to
{
    padding: 5px;
    justify-content: end;
    display: flex;
    font-size: 12px;
    background-color: rgb(108, 116, 255);
    border-radius: 10px;
    color: rgb(133, 214, 255);
}