.portfolio_full
{
    height: 100%;
  
}

.BG_IMG
{
    height: 100%;
    width: 100%;
  
   
}

.BG_IMG img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.writing
{
    z-index: 9;
    position: fixed;
    top: 0%;
    height: 100%;
    display: flex;
    align-items: center;
}

.let_start
{
    font-size: 50px;
    font-weight: 1000;
    width: 50%;
    padding: 10px;
    text-align: center;
    color: rgb(79, 114, 255);
}

.we_welcome
{
    width: 50%;
    padding: 10px;
    text-align: justify;
    color: rgb(173, 173, 173);
}

.begin
{
   
    padding: 10px;
    width: 50%;
    text-align: center;
    border-radius: 10px;
    color: rgb(88, 155, 255);
    border-color: rgb(88, 155, 255);
    border-style: solid;
    border-width: 2px;
    transition: transform 0.3s ease; /* Smooth transition */
}

/* App.css */
.hoverDiv {
    width: 100px;
    height: 100px;
    background-color: lightblue;
 
  }
  
  .begin:hover {
    transform: scale(1.05); /* Scales the div to 1.5 times its original size */
    cursor: pointer; /* Changes the cursor */
    background-color: rgb(88, 155, 255);
    color: rgb(0, 23, 137);
  }
  
  .pink
  {
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0%;
    backdrop-filter: blur(10px);
    background: rgba(75, 75, 75, 0.3);
    z-index: 9;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .inner_Pink
  {
    width: 40%;
    backdrop-filter: blur(10px);
    background: rgba(75, 75, 75, 0.3);
    padding: 10px;
    border-radius: 20px;
    animation: rises 1s;
  }

  @keyframes rises {
    0%{transform: scale(0);}
    

   
   

   
    100%{transform: scale(1);}
}

  .fill_In
  {
    text-align: center;
    color: rgb(150, 150, 150);
    font-weight: 700;
    font-size: 20px;
  }

  .submit
  {
    background-color: rgb(88, 155, 255);
    width: 90%;
    color: rgb(0, 43, 95);
    border-radius: 20px;
    padding: 10px;
    text-align: center;
    margin: 0 auto;
    display: block;
    transition: transform 0.3s ease; /* Smooth transition */
  }

  .submit:hover {
    transform: scale(1.05); /* Scales the div to 1.5 times its original size */
    cursor: pointer; /* Changes the cursor */
  
  }

  @media only screen and (max-width: 700px)
  {
    .let_start
    {
        font-size: 50px;
        font-weight: 1000;
        width: 100%;
        padding: 10px;
        text-align: center;
        color: rgb(79, 114, 255);
        text-shadow: 2px 4px 6px rgba(0, 0, 0, 0.5);

    }
    
    .we_welcome
    {
        width: 100%;
        padding: 10px;
        text-align: justify;
        color: rgb(173, 173, 173);
        backdrop-filter: blur(10px);
        background: rgba(75, 75, 75, 0.3);
    }
    
    .begin
    {
       
        padding: 10px;
        width: 95%;
        text-align: center;
        border-radius: 10px;
        color: rgb(88, 155, 255);
        border-color: rgb(88, 155, 255);
        border-style: solid;
        border-width: 2px;
        margin: auto 0;
        display: block;
    }

    .inner_Pink
    {
      width: 90%;
  
    }
    
  }
