.Viewrs
{
    height: 50%;
    width: 100%;
    backdrop-filter: blur(5px);
    background: rgba(0, 0, 0, 0.3);
    position: fixed;
    top: 50%;
    z-index: 9;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    transform: scaleY(0); /* Start with no vertical scale */
    transform-origin: bottom; /* Stretch from the bottom */
    animation: stretchUp 1s ease forwards; /* Animate upwards */
    overflow: hidden;
}

@keyframes stretchUp {
    0% {
      transform: scaleY(0);
    }
    50% {
      transform: scaleY(1.2);
    }
    100% {
        transform: scaleY(1);
      }
