img
{
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 20px;
}

/* Add this to your CSS file (Send_pp.css) */
.spinner-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
  }
  
  .loading-spinner {
    border: 8px solid rgba(109, 156, 255, 0.1);
    border-left-color: #1d1f83;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    100% {
      transform: rotate(360deg);
    }
  }
  