.whole_edit
{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.pic_div
{
    height: 150px;
    width: 150px;
    border-radius: 10px;
    background-color: rgb(118, 118, 118);
    margin-bottom: 30px;
    margin-top: 20px;
}

.name_inp
{
    border-width: 1px;
    border-color: rgb(72, 99, 255);
    border-style: solid;
    border-radius: 10px;
    background-color: transparent;
}

.edit_name
{
    color: rgb(72, 99, 255);
}

.chaneg_PP
{
    padding: 10px;
    color: rgb(72, 99, 255);
}

.Edit_profile
{
    width: 100%;
    padding: 10px;
    border: none;
    background-color: rgb(156, 145, 145);
    border-radius: 10px;
    color: rgb(140, 201, 255);
    margin-bottom: 10px;
}

.pic_div img
{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.edit_name
{
    display: flex;
    align-items: center;
    gap: 10px;
}